import React , {useState} from "react"
import Books from "./Books"
function  Search() {
    const [query, setQuery] = useState("")
    let [books, setBooks] = useState([])

    const handleChange = (e) => {
        setQuery(e.target.value)
    }

    const searchBook = async (event) => {
        event.preventDefault()
        const url = `https://www.googleapis.com/books/v1/volumes?q=${query}`
        try {
            const response = await fetch(url)
            const data = await response.json()
            setBooks(data.items)
            console.log(books)
        } catch (error){
            console.log(error)
        }
        
    }


    return(
        <div>
                <div className="input-group mb-3">
                    <input type="text" name="query" placeholder="i.e. You don't know JS" value={query} onChange={handleChange} 
                    className="form-control"/>
                    <button type="button" className="btn btn-outline-success" onClick={searchBook}>Search</button>
                    
                    
                    
                </div>
                
                <div className="card-list">
                    {books ? books.map(book => <Books 
                    title={book.volumeInfo.title}
                    key={book.id}
                    image={book.volumeInfo.imageLinks.thumbnail ? book.volumeInfo.imageLinks.thumbnail : null }
                    link={book.volumeInfo.infoLink}
                />) : null}
                </div>
                        
        </div>
            
        
    )
}

export default Search