import React from "react"

function Books(props) {
    console.log(props)
    const style = {
        "width": "13rem",
        "margin-top": "2rem",
 }
    return(
            <div className="card" style={style}>
                <div className="card-body">
                <a href={props.link} target="_blank" rel="noreferrer"><img src={props.image} alt={props.title} className="card-img-top img-thumbnail"/></a>
                    <p className='card-title'>{props.title}</p>
            </div>   
        </div>
        
    )
}

export default Books

